@charset "UTF-8";
/* Ресет */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
}

/* Шрифты */
@font-face {
  font-family: "Mazzard";
  src: url("../fonts/MazzardMBold.eot");
  src: url("../fonts/MazzardMBold.eot") format("embedded-opentype"), url("../fonts/MazzardMBold.woff2") format("woff2"), url("../fonts/MazzardMBold.woff") format("woff"), url("../fonts/MazzardMBold.ttf") format("truetype"), url("../fonts/MazzardMBold.svg#MazzardMBold") format("svg");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "GothamPro";
  src: local("GothamPro Regular"), local("GothamPro-Regular"), url("../fonts/GothamPro.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "GothamPro";
  src: local("GothamPro Light"), local("GothamPro-Light"), url("../fonts/GothamPro-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "GothamPro";
  src: local("GothamPro Medium"), local("GothamPro-Medium"), url("../fonts/GothamPro-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "GothamPro";
  src: local("GothamPro Bold"), local("GothamPro-Bold"), url("../fonts/GothamPro-Bold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}
/* Глобальное */
.wrapper {
  min-height: 100%;
}

.container {
  max-width: 1253px;
  padding: 0 15px;
  margin: 0 auto;
}

* {
  font-family: "GothamPro";
}

.globalTitle {
  font-family: "GothamPro";
  font-size: 32px;
  color: #0D2938;
  font-weight: 700;
}

body.noScroll {
  overflow: hidden;
}

.titleLine {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

input,
textarea {
  background: #F4F4F4;
  border-radius: 7px;
  padding: 18px 25px;
  border: none;
  outline: none;
  color: #767676;
}
input::placeholder,
textarea::placeholder {
  color: #767676;
}

.back {
  color: black;
  display: flex;
  align-items: center;
}
.back__text {
  transition: 0.3s all;
  margin-left: 15px;
  font-style: italic;
}
.back:hover .back__text {
  transform: translateX(5px);
}

/* UI компоненты */
/* Модальное окно */
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3;
  pointer-events: none;
  transition: 0.7s ease all;
  transform: translateY(-100vh);
  opacity: 0;
}
.modal__inner {
  min-width: 532px;
  min-height: 495px;
  max-width: none;
  max-height: none;
  background: white;
  border-radius: 15px;
  padding: 47px 76px 33px 81px;
  transition: 0.3s all;
  pointer-events: none;
}
.modal__background {
  z-index: 2;
  background: rgba(0, 0, 0, 0.5);
  transition: 0.5s all;
  opacity: 0;
  height: 100%;
  width: 100%;
  position: fixed;
  pointer-events: none;
  top: 0;
  left: 0;
}
.modal__background.active {
  opacity: 1;
  pointer-events: all;
}
.modal.active {
  transform: translateY(0);
  opacity: 1;
  pointer-events: all;
}
.modal.active .modal__inner {
  pointer-events: all;
}
.modal__inner.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.modal__title {
  text-align: center;
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 12px;
}
.modal__title.bigMargin {
  margin-bottom: 46px;
}
.modal__description {
  text-align: center;
  font-size: 14px;
  margin-bottom: 20px;
}
.modal__form {
  width: 330px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin-bottom: 34px;
}
.modal__form * {
  margin-bottom: 8px;
}
.modal__form:last-child {
  margin-bottom: 0;
}
.modal__input, .modal__textarea, .modal__button {
  width: 100%;
}
.modal__textarea {
  max-height: 200px;
  height: 92px;
  resize: vertical;
}
.modal__button {
  padding: 17px 45px;
  font-size: 18px;
  font-weight: 700;
}
.modal__subdescription {
  width: 444px;
  font-size: 14px;
  color: black;
  text-align: center;
}
.modal a {
  color: #9F9F9F;
  position: relative;
  text-decoration: underline;
}

/* Кнопки */
.button {
  background: #00B7C3;
  color: white;
  transition: 0.3s all;
  cursor: pointer;
  border: none;
  border-radius: 8px;
}
.button:hover {
  background: #008799;
}

/* Анимации */
@keyframes lineAnim {
  from {
    transform: rotate(0);
  }
  25% {
    transform: rotate(15deg);
  }
  50% {
    transform: rotate(0);
  }
  75% {
    transform: rotate(-15deg);
  }
  to {
    transform: rotate(0);
  }
}
/* Хедер */
.header {
  background: #F9F9F9;
}
.header__inner {
  padding: 36px 236px 33px 196px;
  display: flex;
  justify-content: space-between;
}
.header__list {
  margin-right: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__list .header__item:not(:last-of-type) {
  margin-right: 42px;
}
.header__link {
  color: #3f3f3f;
  text-transform: uppercase;
  font-size: 14px;
  transition: 0.3s all;
}
.header__link:hover {
  color: black;
}
.header__link-line {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.header__link-line.reverse svg {
  transform: rotate(180deg);
}
.header__link-line.reverse:hover svg {
  animation: none;
}
.header__link-line svg {
  transition: 0.3s all;
}
.header__link-line a {
  margin-right: 10px;
}
.header__link-line:hover svg {
  animation: lineAnim 0.35s;
}
.header__right {
  display: flex;
  align-items: center;
}
.header__phone {
  color: #3f3f3f;
  margin-right: 58px;
}
.header__button {
  padding: 17px 35px;
  text-transform: uppercase;
  font-size: 18px;
  margin-right: 54px;
}
.header__social {
  display: flex;
}
.header__social-link path {
  transition: 0.3s all;
}
.header__social-link:hover path {
  fill: #00B7C3;
}
.header__social-link:not(:last-of-type) {
  margin-right: 17px;
}
.header__menu {
  padding: 70px 0px 183px;
  background: #F9F9F9;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  display: none;
  overflow-y: scroll;
}
.header__menu::-webkit-scrollbar {
  width: 4px;
}
.header__menu::-webkit-scrollbar-thumb {
  background: #00b7c3;
}
.header__menu::-webkit-scrollbar-track {
  background-color: #f2f2f2;
}
.header__mobileMenu {
  background: #f9f9f9;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.header__mobileMenu-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  background: #f9f9f9;
  z-index: 2;
  margin-top: 112px;
}
.header__mobileMenu-list {
  margin-top: 50px;
  margin-bottom: 100px;
}
.header__mobileMenu-item {
  text-align: center;
}
.header__mobileMenu-item:not(:last-of-type) {
  margin-bottom: 40px;
}
.header__mobileMenu-link {
  font-size: 16px;
  color: #3F3F3F;
  transition: 0.3s all;
  text-transform: uppercase;
}
.header__mobileMenu-link:hover {
  color: black;
}
.header__mobileMenu-button {
  padding: 17px 30px;
  text-transform: uppercase;
  font-size: 18px;
}

.menu__title {
  margin-bottom: 35px;
}

/* Оснвоное */
/*---- Начальная страница -----*/
/* Начальный блок со слайдером */
/* Сам блок и стрелки */
.sliderMain {
  background: #F9F9F9;
}
.sliderMain__inner {
  padding: 150px 225px 75px 169px;
  position: relative;
}
.sliderMain__arrow {
  padding: 16px 18px 15px 18px;
  background: rgba(211, 211, 211, 0.6);
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.sliderMain__leftArrow {
  left: 92px;
}
.sliderMain__rightArrow {
  right: 124px;
}
.sliderMain .slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 74px;
}
.sliderMain .slick-dots li:not(:last-of-type) {
  margin-right: 10px;
}
.sliderMain .slick-dots li.slick-active .slider__dots {
  transform: scale(1.5);
  background: black;
}
.sliderMain .slick-dots li.slick-active + li .slider__dots {
  transform: scale(1.2);
  background: rgba(0, 0, 0, 0.7);
}

/* Настройки внутри самого слайдера */
.slider__slide {
  display: flex;
  justify-content: space-between;
  margin: 0 126px;
}
.slider__dots {
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.35);
  transition: 0.3s all;
}

/* Слайд */
.slide__title {
  color: #0D2938;
  font-size: 48px;
  margin-bottom: 25px;
  font-weight: 700;
  max-width: 745px;
  line-height: 60px;
}
.slide__left {
  margin-bottom: 23px;
}
.slide__descirption {
  color: #A6A6A6;
  margin-bottom: 69px;
  font-weight: 300;
}
.slide__button {
  padding: 20px 90px;
  font-weight: 700;
  font-size: 18px;
}
.slide__img {
  width: 414px;
  height: 300px;
  object-fit: scale-down;
}

/* Услуги */
.services {
  background: #F9F9F9;
  padding-bottom: 135px;
}
.services__showAll {
  display: flex;
  align-items: center;
}
.services__showAll:hover svg {
  transform: translateX(2px);
}
.services__showAll-text {
  font-size: 14px;
  color: #3F3F3F;
  text-transform: uppercase;
  margin-right: 12px;
}
.services__showAll-arrow svg {
  transition: 0.3s ease-in all;
}
.services__titleLine {
  margin-bottom: 40px;
}
.services__box {
  display: grid;
  grid-template-columns: repeat(3, 392px);
  grid-template-rows: none;
  gap: 24px;
  align-content: center;
}
.services__service {
  padding: 33px 45px 37px 54px;
  background: white;
  border-radius: 12px;
  box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
  transition: 0.3s all;
}
.services__service .car {
  height: 64px;
}
.services__service > svg {
  margin-bottom: 29px;
}
.services__service > svg * {
  transition: 0.3s all;
}
.services__service .service__line svg {
  transition: 0.3s all;
}
.services__service:hover {
  box-shadow: 0px 1px 25px rgba(1, 4, 5, 0.062);
}
.services__service:hover > svg path {
  fill: rgba(0, 0, 0, 0.5);
}
.services__service:hover > svg rect {
  stroke: rgba(0, 0, 0, 0.4);
}
.services__service:hover .service__line svg {
  transform: translateX(10px);
}

.service__line {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.service__line svg {
  margin-left: 33px;
}
.service__title {
  font-size: 24px;
  font-weight: 600;
  color: black;
}
.service__description {
  font-size: 14px;
  color: #A6A6A6;
  font-weight: 300;
  line-height: 20px;
}

/* Партнеры */
.partners {
  background: #F9F9F9;
  padding-bottom: 135px;
}
.partners .js-marquee {
  display: flex;
  align-items: center;
}
.partners__inner {
  display: flex;
  align-items: center;
  overflow: hidden;
}
.partners img:not(:last-of-type) {
  margin-right: 35px;
}

/* Баннеры */
/* В целом блок */
.banners {
  background: #F9F9F9;
  padding-bottom: 101px;
}
.banners__inner {
  display: flex;
  flex-direction: column;
}
.banners__bottom {
  display: flex;
  justify-content: space-between;
}
.banners__right {
  width: 100%;
  margin-left: 39px;
  display: flex;
  flex-direction: column;
}

/* Отдельный баннер */
.banner {
  padding: 32px 0px 0px 72px;
  background: white;
  box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
  border-radius: 12px;
  position: relative;
}
.banner__line {
  display: flex;
  align-items: center;
  margin-bottom: 33px;
}
.banner__right {
  position: absolute;
  top: -64px;
  right: 90px;
}
.banner__big {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  height: 100%;
  width: 100%;
  padding-top: 57px;
}
.banner__mini {
  padding-top: 20px;
}
.banner__mini.firstBanner {
  min-height: 228px;
  margin-bottom: 33px;
}
.banner__mini.secondBanner {
  min-height: 307px;
}
.banner__title {
  font-size: 24px;
  font-weight: 500;
}
.banner__title.big {
  text-transform: uppercase;
  font-size: 28px;
  margin-bottom: 26px;
}
.banner__description {
  color: #656F7C;
  margin-top: 18px;
  margin-bottom: 37px;
  max-width: 506px;
  line-height: 22px;
}
.banner__description.long {
  max-width: 717px;
}
.banner__top.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.banner__top.big {
  padding-left: 72px;
  height: 35%;
  position: relative;
}
.banner__bottom.big {
  height: 65%;
  position: relative;
}
.banner__img-wrapper {
  padding: 16px 20px;
  box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
  border-radius: 19px;
  background: #F5F5F5;
  margin-right: 41px;
}
.banner__background {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 0 0 12px 12px;
}
.banner__logo {
  position: absolute;
  top: 26px;
  left: 74px;
  z-index: 2;
}
.banner__topEffect {
  height: 40%;
  width: 100%;
  background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
  position: absolute;
  top: 0;
  left: 0;
}

/* Вопросы */
/* Весь блок с вопросами */
.questions {
  padding: 68px 0px 86px;
}
.questions__top {
  position: relative;
}
.questions__bottom {
  margin-top: 47px;
}
.questions__content {
  margin-top: 60px;
}
.questions__arrow {
  padding: 15px 17px;
  border-radius: 50%;
  background: rgba(211, 211, 211, 0.6);
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.questions__leftArrow {
  left: 0;
}
.questions__rightArrow {
  right: 0;
}
.questions__buttons {
  width: 90%;
  margin: 0 auto;
}
.questions__button {
  padding: 27px 72px;
  font-size: 22px;
  background: #F5F5F5;
  color: black;
  box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
  border-radius: 39px;
  font-weight: 500;
  transition: 0.3s all;
}
.questions__button.select {
  background: rgba(0, 183, 195, 0.83);
  color: white;
  cursor: default;
}
.questions__button:not(:last-of-type) {
  margin-right: 17px;
}
.questions__steps {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.questions__wrapper {
  display: none;
}

/* Действия */
.question__step {
  width: 32%;
  background: #F5F5F5;
  box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
  border-radius: 12px;
  padding: 45px 67px 0px 42px;
  min-height: 475px;
  position: relative;
}
.question__step-title {
  font-size: 19px;
  font-weight: 500;
  color: black;
  margin-bottom: 23px;
}
.question__step-description {
  color: #656F7C;
  margin-bottom: 55px;
  line-height: 22px;
}
.question__step a {
  color: #00B7C3;
}
.question__subdescription {
  display: flex;
  line-height: 22px;
}
.question__linkInc {
  margin-right: 13px;
}
.question__number {
  color: #DBDBDB;
  font-size: 120px;
  position: absolute;
  bottom: -22px;
  right: 35px;
}

/* Отзывы */
/* Блок со всеми отзовами */
.reviews {
  background: #F9F9F9;
  padding: 180px 0px 48px;
}
.reviews__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.reviews__arrows {
  display: flex;
}
.reviews__arrow {
  cursor: pointer;
  background: rgba(211, 211, 211, 0.6);
  padding: 15px 18px;
  border-radius: 50%;
}
.reviews__leftArrow {
  margin-right: 18px;
}
.reviews__bottom {
  margin-top: 38px;
}

/* Отдельный блок с отзывом */
.review__box {
  background: #FFFFFF;
  box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
  border-radius: 12px;
  padding: 30px 28px 12px 29px;
}
.review__box:not(:last-of-type) {
  margin-right: 17px;
}
.review__line {
  display: flex;
  align-items: center;
  margin-bottom: 19px;
}
.review__title {
  font-weight: 500;
  margin-right: 20px;
}
.review__star {
  margin-right: 2px;
}
.review__number {
  font-size: 14px;
  color: #474747;
  margin-left: 3px;
}
.review__description {
  font-size: 13px;
  color: #474747;
  line-height: 17px;
  margin-bottom: 30px;
}
.review__city {
  font-size: 12px;
  color: #BABABA;
}

/* FAQ */
.faq {
  padding-bottom: 105px;
  background: #F9F9F9;
}
.faq__inner {
  background: #FFFFFF;
  box-shadow: 0px 1px 25px rgba(13, 41, 56, 0.02);
  border-radius: 12px;
  padding: 52px 69px 60px 60px;
}
.faq__title {
  margin-bottom: 54px;
}
.faq__description {
  font-weight: 300;
  line-height: 22px;
}
.faq__top {
  display: flex;
  justify-content: space-between;
}
.faq__miniTitle {
  font-size: 20px;
  color: #0D2938;
  font-weight: 700;
}
.faq__close {
  transition: 0.3s all;
  cursor: pointer;
}
.faq__close.select {
  transform: rotate(45deg);
}
.faq__bottom {
  margin-top: 20px;
  display: none;
}
.faq__line {
  width: 100%;
  border: 0.5px solid #EEEEEE;
  margin-top: 30px;
}
.faq__question:not(:last-of-type) {
  margin-bottom: 32px;
}

/*---- Страница с продуктом ----*/
/* Верхний блок с самим продуктом */
.product {
  background: #F9F9F9;
  padding: 60px 0 109px 0;
}
.product__back {
  margin-bottom: 34px;
}
.product__product {
  display: flex;
}
.product__img {
  width: 392px;
  height: 352px;
  object-fit: cover;
  border-radius: 12px;
}
.product__right {
  margin-left: 62px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.product__description {
  font-size: 18px;
  color: #656F7C;
  line-height: 25px;
}
.product__button {
  padding: 17px 42px;
  text-transform: uppercase;
  font-size: 18px;
  align-self: flex-start;
}

/* Документы при оформлении страховки */
.document {
  padding: 68px 0px 95px;
}
.document__title {
  margin-bottom: 90px;
}
.document__imgs, .document__texts {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.document__imgs {
  align-items: center;
}
.document__img-wrapper, .document__text-wrapper {
  width: 25%;
  display: flex;
  justify-content: center;
}
.document__img {
  height: 240px;
  object-fit: scale-down;
}
.document__text {
  text-align: center;
  margin-top: 20px;
  font-size: 20px;
  color: #0D2938;
  font-weight: 700;
}

/* Список */
.price {
  background: #F9F9F9;
  padding: 87px 0px 108px;
}
.price__title {
  margin-bottom: 45px;
}
.price__content {
  display: flex;
  align-items: flex-start;
}
.price__list {
  margin-left: 50px;
}
.price__list:not(:last-of-type) {
  margin-right: 150px;
}
.price__item {
  font-size: 20px;
  color: #656F7C;
  position: relative;
